import React from "react"

const SideMenu = props => {
  return (
    <div key={1} id="side-menu" class="side-nav">
      <div class="btn-close" onClick={() => props.onClick()}>
        &times;
      </div>

      <div className="container">
        <div>Specials</div>
        <div>Appetizers</div>
        <div>Entrees</div>
        <div>Beverages</div>
      </div>
    </div>
  )
}

export default SideMenu
