import React, { Component } from "react"
import "./Menu.css"
// import "./sideMenu.css"

import {
  Transition,
  CSSTransition,
  TransitionGroup,
} from "react-transition-group"
import FirstChild from "./FirstChild.js"
import SideMenu from "./SideMenu.js"

class Menu extends Component {
  constructor(props) {
    super(props)
    this.state = {
      menuOpen: false,
    }
    this.onClick = this.onClick.bind(this)
  }

  onClick() {
    this.setState({ menuOpen: !this.state.menuOpen }, () =>
      console.log(this.state.menuOpen)
    )
  }
  render() {
    const { menuOpen } = this.state
    return (
      <>
        <a class="nav-link text-light" href="#menu" onClick={this.onClick}>
          MENU
        </a>
        <CSSTransition in={menuOpen} classNames="menu" unmountOnExit>
          <SideMenu onClick={this.onClick} />
        </CSSTransition>
      </>
    )
  }
}

export default Menu
