import React, { Component } from "react"
import "./Contact.css"

class Contact extends Component {
  constructor(props) {
    super(props)
    this.state = {
      show: false,
    }
    this.showModal = this.showModal.bind(this)
    this.hideModal = this.hideModal.bind(this)
  }

  showModal() {
    this.setState({ show: true }, () => console.log(this.state.show))
  }

  hideModal() {
    this.setState({ show: false })
  }
  render() {
    const showHideClassName = this.state.show
      ? "modal1 display-block"
      : "modal1 display-none"
    return (
      <>
        <a class="nav-link text-light" href="#contact" onClick={this.showModal}>
          CONTACT
        </a>
        <div className={showHideClassName}>
          <section className="modal-main">
            <div class="company-info">
              <ul>
                <li>
                  <i class="fa fa-road"></i>88 Somewhere st
                </li>
                <li>
                  <i class="fa fa-phone">(888) 888-8888</i>
                </li>
                <li>
                  <i class="fa fa-envelope">SkylineCafe@gmail.com</i>
                </li>
              </ul>
              <ul>
                <li class="favicon">
                  <a class="instagram" href="#">
                    <i class="fab fa-instagram fa-1x"></i>
                  </a>
                  <a class="facebook" href="#">
                    <i class="fab fa-facebook-f fa-1x"></i>
                  </a>
                  <a class="twitter" href="#">
                    <i class="fab fa-twitter fa-1x"></i>
                  </a>
                  <a class="yelp" href="#">
                    <i class="fab fa-yelp fa-1x"></i>
                  </a>
                </li>
              </ul>
            </div>
            <button onClick={this.hideModal}>close</button>
          </section>
        </div>
      </>
    )
  }
}

export default Contact
